"use client";
import { useState } from "react";

const SetupFileUpload = () => {
  const [file, setFile] = useState<File | null>(null);
  const [preview, setPreview] = useState<string | null>(null);
  const [showToast, setShowToast] = useState(false);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0] || null;
    setFile(selectedFile);

    if (selectedFile) {
      const objectURL = URL.createObjectURL(selectedFile);
      setPreview(objectURL);
    }
  };

  const handleUpload = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!file) {
      setShowToast(true);
      setTimeout(() => setShowToast(false), 3000);
      return;
    }

    const formData = new FormData();
    formData.append("file", file);

    const res = await fetch("/admin/settings/uploads", {
      method: "POST",
      body: formData,
      headers: {
        Authorization: "Bearer SECRET_ADMIN_KEY", // Erstat med dit eget auth-system
      },
    });

    const data = await res.json();
    if (res.ok) {
      setShowToast(true);
      setTimeout(() => setShowToast(false), 3000);
    } else {
      setShowToast(true);
      setTimeout(() => setShowToast(false), 3000);
    }
  };

  return (
    <div className="p-4 w-full">
      <h3 className="text-lg md:text-xl font-semibold ">Skift introbillede</h3>
      <div className="flex flex-col md:flex-row items-center md:justify-between lg:justify-start w-full gap-10 ">
        <div className="md:flex-1 flex flex-col gap-3 items-start w-full">
          <label className="form-control w-full max-w-xs">
            <div className="label">
              <span className="label-text">Vælg nyt billede</span>
            </div>
            <input
              type="file"
              className="file-input file-input-bordered w-full max-w-xs"
              onChange={handleFileChange}
            />
          </label>
          <button
            onClick={handleUpload}
            className="btn btn-primary"
            disabled={!file}
          >
            Upload
          </button>
        </div>
        <div className="md:flex-1">
          {preview && (
            <div className="relative w-64 h-36 overflow-hidden rounded-md border">
              <img
                src={preview}
                alt="Preview"
                className="absolute top-0 left-0 w-full h-full object-cover"
              />
            </div>
          )}
        </div>
      </div>
      {showToast && (
        <div className="toast bottom-20 md:bottom-0 toast-end">
          <div className="alert alert-success text-neutral-content">
            <span>Fil uploadet!</span>
          </div>
        </div>
      )}
    </div>
  );
};

export default SetupFileUpload;
